body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f8f8f8;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: url('https://i.ibb.co/YhF7z9c/Green-and-Cream-Photographic-Blogger-Bio-Link-Website-2.png') no-repeat center center/cover;
  padding: 20px; /* Add padding to avoid content touching screen edges */
}

.content {
  display: flex;
  flex-direction: row; /* Display side by side on larger screens */
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  padding: 20px;
  border-radius: 10px;
  max-width: 90%; /* Restrict the width */
  width: 600px; /* Set a default width */
  box-sizing: border-box; /* Include padding and border in width */
}

.footer {
  position: absolute;
  bottom: 0;
  width: 20%;
  text-align: center;
  padding: 10px;
}

.footer img {
  vertical-align: middle;
  margin-right: 1px;
}

.logo {
  margin-right: 50px;
}

.logo img {
  width: 200px; /* Make the image smaller for mobile */
}

.text {
  color: white;
  text-align: center;
}

.text h1 {
  margin: 0;
  font-size: 32px; /* Adjust font size for better scaling */
}

.text p {
  margin: 5px 0;
  font-size: 16px; /* Adjust font size */
}

.button {
  margin: 20px 0;
}

.button a {
  text-decoration: none;
  color: white;
  border: 2px solid white;
  padding: 10px 20px;
  border-radius: 5px;
  transition: background 0.3s, color 0.3s;
}

.button a:hover {
  background: white;
  color: black;
}

.social-icons {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.social-icons a {
  color: white;
  font-size: 24px;
  margin: 0 10px;
  transition: color 0.3s;
}

.social-icons a:hover {
  color: #ddd;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .content {
    flex-direction: column; /* Stack content vertically on smaller screens */
    text-align: center; /* Center align text on mobile */
  }

  .logo {
    margin-right: 0; /* Remove side margin on smaller screens */
    margin-bottom: 20px; /* Add bottom margin for spacing */
  }

  .logo img {
    width: 200px; /* Smaller image on mobile */
  }

  .text h1 {
    font-size: 28px; /* Smaller font size for mobile */
  }

  .text p {
    font-size: 14px; /* Smaller font size for mobile */
  }

  .button a {
    padding: 8px 16px; /* Smaller button padding on mobile */
    font-size: 14px; /* Smaller font size for buttons */
  }

  .social-icons a {
    font-size: 20px; /* Adjust icon size */
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* Other styles */
  }

}
